import { useEffect, useState } from 'react'
import { useSelector, useDispatch } from 'react-redux'
import { useNavigate } from 'react-router-dom'
import { LoginSignupForm } from '../cmps/login-signup-form.jsx'
import { onLogin } from '../store/user.actions.js'

export const Login = () => {
    const { loggedinUser } = useSelector(state => state.userModule)
    const [isError, setIsError] = useState(false)
    const [currUser, setCurrUser] = useState({
        username: null,
        password: null
    })
    const dispatch = useDispatch()
    const navigate = useNavigate()


    useEffect(() => {
        window.scrollTo({ top: 0, left: 0, behavior: 'auto' })
        if (loggedinUser) navigate('/')
    }, [loggedinUser])

    const handleChange = ({ target }) => {
        setIsError(false)
        const { value, name } = target
        setCurrUser({ ...currUser, [name]: value })
    }

    const onHandleSubmit = async (ev) => {
        ev.preventDefault()

        try {
            await dispatch(onLogin(currUser))
        }
        catch (err) {
            const error = await err
            console.log('error', error)
            setIsError(true)
        }
    }

    return (
        <div className='login-signup-form-container flex column'>
            <h1>Login</h1>
            <LoginSignupForm
                onHandleSubmit={onHandleSubmit}
                handleChange={handleChange}
                isError={isError}
                errorTxt={'Wrong username or password'}
                link={{ path: '/signup', txt: 'Don\'t have an account? Sign Up!' }}
            />
        </div>
    )
}